import Vue from 'vue';
import Vuex from 'vuex';
import article from './modules/article';
import user from './modules/user';
import app from './modules/app';

Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    article: article,
    user: user,
    app: app
  }
});



